body {
  display: flex;
  align-items: center;
  margin: 0;
  background: #313131;
  height: 100vh;
  font-family: Calibri, sans-serif;
}

.container {
  position: relative;
  display: inline-block;
  margin: auto;
  text-align: center;
}

.game-container {
  position: relative;
  border: 20px solid rgba(0, 0, 0, 0.18);
  border-radius: 6px;
}

svg {
  display: block;
  background: #fff;
}

.score {
  margin-top: 12px;
  text-align: right;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 300;

  span {
    display: inline-block;
    transition: all 0.6s ease-out;
    background: rgba(0, 0, 0, 0.18);
    padding: 6px 8px;
    border-radius: 6px;
  }

  &.active {
    span {
      color: hotpink;
      transform: scale(1.15);
      background: rgba(0, 0, 0, 0.24);
    }
  }
}

h1 {
  color: #fff;
  text-align: center;
  font-size: 3.8rem;
  font-weight: 900;
  display: inline-block;
  line-height: 1;
  background: linear-gradient(135deg, rgba(255, 255, 255, 1) 0%, #ff148a 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.game-over {
  display: none;
  position: absolute;
  background: rgba(0, 0, 0, 0.9);
  padding: 20px 40px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  color: #fff;

  p {
    font-size: 2.6rem;
    font-weight: 900;
    background: linear-gradient(135deg, rgba(255, 255, 255, 1) 0%, #ffcae5 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &.active {
    display: block;
  }
}
